export default {
	data() {
		return {
			loginMode: 1,
			usernameType:"text",
			codeBut:"获取验证码",
			loginImage:"../../static/logo.png",
			codeClick:true,
			phone:"",
			password:"",
			code:"",
		}
	},
	onLoad() {
		
	},
	methods: {
		loginCode() {
			this.loginMode = 2
			this.usernameType = "number"
		},
		loginPhone() {
			this.loginMode = 1
			this.usernameType = "text"
		},
		// 手机号输入
		phoneInput(e){
			this.phone = e.detail.value
		},
		// 密码输入
		passwordInpur(e){
			this.password = e.detail.value
		},
		// 验证码输入
		codeInput(e){
			this.code = e.detail.value
		},
		// 点击登录
		loginUp(){
			var _that = this
			if(_that.phone == ""){
				uni.showToast({
					title: '请输入用户名',
					icon: 'none',
					duration:1000
				});
			}else if(_that.password == "" && _that.loginMode == 1){
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					duration:1000
				});
			}else if(_that.code == "" && _that.loginMode == 2){
				uni.showToast({
					title: '请输入验证码',
					icon: 'none',
					duration:1000
				});
			}else{
				var loginUser = {
					"account": this.phone,
					"password": this.password,
					"type":0
				}
				//这里请求接口
				uni.request({
					url: this.serverUrl + "tb/tbuser/login",
					method: 'POST',
					data: loginUser,
					success: res => {
						console.log(res);
						var data = res.data;
						if(data.code == 200){
							console.log("登录成功");
							uni.setStorageSync("globalUser", data.user);
							uni.navigateBack({
								delta: 1
							});
						}else if(data.code == 500){
							uni.showToast({
								title: data.msg
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
				console.log(_that.phone,_that.password,_that.code)
			}
		},
		// 获取验证码
		getCode(){
			var _that = this
			const mobsms = uni.requireNativePlugin('WB-MobSms');
			// 倒计时
			if(_that.codeClick){
				if(_that.phone[0] != 1 || _that.phone.length != 11){
					uni.showToast({
						title: '请输入正确的手机号',
						icon: 'none',
						duration: 800
					});
				}else{
					_that.codeClick = false
					// 发送短信
					mobsms.sendSms(
						{zone: '86',phone_number: _that.phone},
						result => {if (result.status == 'success') {
								uni.showToast({
									title: '验证码获取中',
									icon: 'loading',
									duration: 800
								});
							}
						}
					);
					// 倒计时开始
					var daojishi = 60
					var xianshi = setInterval(function(){
						_that.codeBut = "重新发送"+daojishi;
						daojishi--
						if(daojishi<=0){
							_that.codeBut = "重新发送"
							_that.codeClick = true
							clearInterval(xianshi);
						}
					},1000)
				}
			}
		},
		// 微信登录
		weixinlogin(){
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
			    console.log(loginRes.authResult);
			    // 获取用户信息
			    uni.getUserInfo({
			      provider: 'weixin',
			      success: function (infoRes) {
					console.log(infoRes)
			        console.log('用户昵称为：' + infoRes.userInfo.nickName);
			      }
			    });
			  }
			});
		},
		// qq登录
		qqlogin(){
			var _that = this
			uni.login({
			  provider: 'qq',
			  success: function (loginRes) {
				  uni.getUserInfo({
				    provider: 'qq',
				    success: function (infoRes) {
				  	console.log(infoRes)
						_that.loginImage = infoRes.userInfo.figureurl_qq_2
				    }
				  });
			  }
			});
		},
		// 微博登录
		weibologin(){
			uni.login({
				provider:"sinaweibo",
				success: function (loginRes) {
				  console.log(loginRes.authResult);
				}
			})
		}
	}
}
